@import "../../scss/variable";
.web__gradient {
     width: calc(66.5% + 24px);
     height: calc(100vh - 12px);
     overflow: hidden !important;
     position: relative;
     margin-top: 12px;
     z-index: 99;
     @media screen and (max-width: 767px) {
          width: 100%;
          z-index: 98;
     }
     & iframe {
          position: absolute;
          width: 100%;
          height: 100%;
     }
}
