@import "../../scss/variable";
.skill {
     &__container {
          position: fixed;
          right: 20px;
          top: 0;
          width: 35%;
          height: 100vh;
          display: flex;
          padding-top: 20px;
          flex-direction: row-reverse;
          z-index: 99;

          & .skill__show {
               display: none;
               position: absolute;
               left: -50px;
               top: 120px;
               font-size: 25px;
               padding: 7px 14px;
               font-weight: 500;
               background: rgba(233, 233, 233, 1);
               color: $TextClr2;
               cursor: pointer;
               border-radius: 5px 0 0 5px;
               & .right {
                    display: none;
                    padding: 5px;
               }
          }
          @media screen and (max-width: 767px) {
               @media screen and (max-width: 767px) {
                    padding-top: 0px;
                    right: 10px;
                    width: 270px;
                    height: 100%;
                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                    align-items: center;
                    background-color: white;
                    transition: 0.25s ease;
                    right: -270px;
                    opacity: 1;
                    &.show {
                         right: 0%;
                         & .left {
                              display: none;
                         }
                         & .right {
                              display: block;
                         }
                    }
                    & .skill__show {
                         display: block;
                    }
               }
          }
     }
     &__avatar {
          display: flex;
          justify-content: flex-start;
          align-items: center;
          padding-top: 140px;
          width: 100%;
          height: 100vh;
          flex-direction: column;
          border-left: 2px solid $activeClr;
          gap: 24px;
          @media screen and (max-width: 767px) {
               gap: 15px;
               padding-left: 20px;
               padding-top: 100px;
               padding-right: 20px;
          }

          & .skill__button {
               &.active1 {
                    background-color: $mainClr;
                    color: white;
                    box-shadow: 0 0 2px $TextClr2;
               }
               transition: 0.35s ease;
               cursor: pointer;
               font-weight: 400;
               background-color: white;
               padding: 2px 10px;
               color: $TextClr2;
               box-shadow: 0 0 1px $TextClr2;
               line-height: 1.2;
               border-radius: 5px;
               font-size: 13.5px;
               line-height: 1.8;
               @media screen and (max-width: 767px) {
                    font-size: 11px;
               }

               &.w {
                    min-width: 200px;
                    @media screen and (max-width: 767px) {
                         min-width: 0px;
                         min-width: 170px;
                    }
               }
               & span {
                    padding-left: 5px;
               }
               &:hover {
                    opacity: 0.95;
               }
          }
     }
}
.skill__h6 {
     font-weight: 500;
     font-size: 17px;
     margin-bottom: -0px;
     padding-top: 0px;
     color: $TextClr2;
     @media screen and (max-width: 767px) {
          font-size: 13px;
     }
}
