@import "../../scss/variable";
.cs__full {
     @include flex(center);
     width: 65%;
     flex-wrap: wrap;
     gap: 50px;
     margin-top: 120px;
     margin-left: 18px;
     row-gap: 20px;
     @media screen and (max-width: 767px) {
          margin-left: -5px;
          width: 100%;
     }
}
.cs__start {
     position: absolute;
     top: 110px;
     font-weight: 500;
     color: $TextClr2;
     font-size: 18px;
     @include flex(center);
     gap: 10px;
     @media screen and (max-width: 767px) {
          font-size: 15.5px;
     }
     & span {
          line-height: 12px;
     }
}
.cs__oderbox {
     display: flex;
     gap: 60px;
     row-gap: 0px;
     position: relative;
     flex-wrap: wrap;
     justify-content: center;
     align-items: center;
     padding-bottom: 150px;
     & .cs__container {
          &:nth-child(1) {
               display: none;
          }
          &:nth-child(2) {
               order: 2;
          }
          &:nth-child(3) {
               order: 3;
          }
          &:nth-child(4) {
               order: 4;
          }
          &:nth-child(5) {
               order: 5;
          }
          &:nth-child(6) {
               order: 6;
          }
          &:nth-child(7) {
               order: 7;
          }
     }
}
.order1 {
     order: 1 !important;
}
.order2 {
     order: 2 !important;
}
.order3 {
     order: 3 !important;
}
.order4 {
     order: 4 !important;
}
.order5 {
     order: 5 !important;
}
.order6 {
     order: 6 !important;
}
.cs__container {
     @include flex(center);
     flex-direction: column;
     margin-top: 20px;
     min-width: 300px;
     gap: 20px;
     margin-top: -50px;

     &.clicked {
          & .cs__mask {
               color: rgb(255, 196, 0);
               text-shadow: 0 0 4px rgb(253, 255, 158);
               animation: fade1 1.5s forwards infinite linear;
               &:active {
                    animation: fade 0.29s forwards infinite linear;
               }
          }
     }
     &:nth-child(7) {
          order: 2;
     }
}

@keyframes fade1 {
     to {
          transform: rotate(2deg);
          text-shadow: 0 0 0px rgb(253, 255, 158);
          opacity: 0.9;
     }
}
.cs__delstart {
     position: absolute;
     top: 70px;
     right: 0;
}
.cs__img {
     margin-top: 50px;
     width: 300px;
     height: 180px;
     position: relative;
     border-radius: 10px;
     overflow: hidden;
     box-shadow: 0 0 4px $TextClr2;
     @media screen and (max-width: 767px) {
          width: 280px;
          height: 170px;
     }

     & b {
          position: absolute;
          right: 6px;
          bottom: 8px;
          color: rgb(68, 255, 208);
          font-size: 12px;
          text-shadow: 0 0 3px $TextClr2;
          font-weight: 500;
     }
     & img {
          @include abs(full);
     }
     & span {
          position: absolute;
          width: 0%;
          height: 4px;
          background-color: rgb(108, 255, 218);
          bottom: 0;
          left: 0;
          border-radius: 5px;
     }
}
.cs__content {
     font-weight: 500;
     font-size: 16px;
     color: $TextClr2;
     position: relative;
     &:hover {
          & .cs__mask {
               opacity: 1 !important;
          }
     }
}
.cs__mask {
     font-size: 24px;
     top: 230px;
     cursor: pointer;
     position: relative;
     z-index: 1;
     right: -132px;
     color: rgb(255, 255, 255);
     padding: 10px 5px;
     transition: 0.35s ease;
     border-radius: 3px;

     @media screen and (max-width: 767px) {
          right: -122px;
          top: 220px;
     }

     &:hover {
          color: rgb(255, 241, 113);
     }
}

.cs__btn {
     background-color: $mainClr;
     padding: 2px 8px;
     font-weight: bold;
     font-size: 13px;
     line-height: 24px;
     color: white;
     border-radius: 10px;
     cursor: pointer;
     display: flex;
     gap: 5px;
     justify-content: center;
     align-items: center;
     transition: 0.35s ease;
     &:hover {
          background-color: rgb(0, 163, 95);
     }
     &:nth-of-type(2):hover {
          background-color: rgb(255, 130, 27);
     }
}
