@import "../../scss/variable";

.header {
     position: fixed !important;
     box-shadow: 0 4px 12px 0 rgba(0, 0, 0, 0.06);
     top: 0;
     left: 0 !important;
     right: 0 !important;
     z-index: 99999;
     padding-bottom: 5px;
     height: 60px;
     width: 100% !important;
     transition: 0.15s ease;
     background-color: $newMain;
     @include flex(center);
     box-shadow: 0 4px 12px 0 rgba(0, 0, 0, 0.06);
     @media screen and (max-width: 767px) {
          justify-content: flex-start;
     }
     &__mam {
          color: rgb(255, 255, 255);
          position: absolute;
          left: 90px;
          font-size: 13.5px;
          font-weight: 500;
          & .header__course {
               border-left: 1px solid white;
               margin-left: 3px;
               padding-left: 5px;
          }
          @media screen and (max-width: 767px) {
               font-size: 12.5px;
               left: 55px;
          }
     }

     &.black {
          background-color: $DarkClr !important;
          box-shadow: 0 4px 0px 0 rgba(0, 0, 0, 0) !important;
          border-bottom: 1px solid $DarkClr !important;
          padding: 28px !important;
          & .header__back {
               color: white !important;
          }
          & .header__bar {
               color: white !important;
          }
          &.green {
               background-color: $DarkClr !important;
          }
          &.blue {
               background-color: $DarkClr1 !important;
               border-bottom: 1px solid $DarkClr1 !important;
          }
          &.pup {
               background-color: $DarkClr2 !important;
               border-bottom: 1px solid $DarkClr2 !important;
          }
          &.org {
               background-color: $DarkClr3 !important;
               border-bottom: 1px solid $DarkClr3 !important;
          }
     }
}
.header.active {
     & .header__scroll {
          height: 0 !important;
     }
     top: 0px;
     height: 50px;
     transition: 0.15s ease;
     padding: 32px;
     left: 0px;
     right: 0px;
     transition: 0.35s ease;
     border-bottom: 1px solid #f0f0f0;
     box-shadow: 0 4px 12px 0 rgba(0, 0, 0, 0.06);
     // background-image: linear-gradient(145deg, $mainClr, $newMain);
     background-color: rgb(255, 255, 255);
     // &::before {
     //      content: "";
     //      background-color: $newMain;
     //      width: 100%;
     //      height: 100%;
     //      position: absolute;
     // }
     & .header__h1 {
          color: $mainClr;
     }
     & .header__bar,
     .header__barclose {
          margin-right: 3px;
          color: $TextClr2;
     }

     & .header__back {
          color: $TextClr2;
          margin-left: 20px;
          user-select: none;
          @media screen and (max-width: 767px) {
               margin-left: 5px;
               color: $TextClr2;
          }

          & i {
               transition: 0.35s ease;
          }
          &:hover i {
               transform: translateX(-3px);
          }
     }

     & .header__list--item {
          & > i {
               padding-right: 15px !important;
               min-width: 30px !important;
          }
          color: $TextClr2;
          &:after {
               content: "";
               position: absolute;
               bottom: 10px;
               left: 0;
               width: 0%;
               background-color: $TextClr2;
               height: 2px;
               opacity: 0.99;
               border-radius: 100rem;
               transition: 0.45s ease;
          }

          &:hover {
               color: $mainClr;
               &:after {
                    background-color: $mainClr;
               }
          }
     }
}
.header__plus-icon {
     font-size: 30px;
     padding-top: 30px;
     width: 100%;
     color: $TextClr2;
     position: relative;
     display: flex;
     justify-content: center;
     &.active2 {
          color: $mainClr;
     }
}
.header__scroll {
     position: absolute;
     width: 0%;
     bottom: 0;
     left: 0;
     height: 4px;
     border-radius: 100rem;
     background-color: $newMain3;
     box-shadow: 0 0 5px $newMain3;
}
.header__color {
     position: absolute;
     top: 50%;
     right: 30px;
     color: white;
     transform: translateY(-50%);
     transition: 0.25s ease;
     & > i {
          cursor: pointer;
     }
     &:hover {
     }
     font-size: 16px;
     font-weight: 400;

     &--box {
          position: absolute;
          top: -10px;
          left: -155px;
          width: 120px;
          height: 80px;
          background: white;
          transition: 0.35s ease;
          display: none;
          flex-wrap: wrap;
          justify-content: center;
          align-items: center;
          box-shadow: 0 0 3px $TextClr2;
          border-radius: 5px;
          & .item {
               font-size: 18px;
               min-width: 50px;
               text-align: center;
               cursor: pointer;
               &:nth-child(1) {
                    color: $DarkClr;
               }
               &:nth-child(2) {
                    color: $DarkClr1;
               }
               &:nth-child(3) {
                    color: $DarkClr2;
               }
               &:nth-child(4) {
                    color: $DarkClr3;
               }
          }
     }
     &.active {
          & .header__color--box {
               display: flex;
          }
     }
}
.header__back {
     @include flex(center);
     gap: 10px;
     cursor: pointer;
     & i {
          transition: 0.35s ease;
     }
     &:hover i {
          transform: translateX(-3px);
     }
     &:active {
          opacity: 0.8;
     }
     & span {
          font-size: 12px;
          @media screen and (max-width: 767px) {
               font-size: 10px;
          }
     }
     position: absolute;
     left: 20px;
     border: none;
     font-size: 20px;
     color: white;
     top: 50%;
     transform: translateY(-50%);
     @media screen and (max-width: 767px) {
          // display: none;
          left: 20px;
     }
}
.header__overlay {
     position: fixed;
     top: 0;
     left: 0;
     bottom: 0;
     width: 0%;
     height: 100%;
     background: rgba(105, 105, 105, 0.07);
     transition: 0.15s ease;
     z-index: -1;
}
.header__fixlogo {
     position: fixed;
     width: 42px;
     height: 42px;
     top: 50%;
     position: absolute;
     transform: translateY(-50%);
     left: 30px;

     @media screen and (max-width: 767px) {
          display: block;
          position: absolute;
          opacity: 1;
          top: 50%;
          transform: translateY(-50%);

          left: 15px;
          z-index: 98;
          margin-right: 0px;
          width: 35px;
          height: 35px;
     }
}
.header__h1 {
     position: absolute;
     top: 50%;
     left: 50%;
     transform: translate(-50%, -50%);
     color: $background;
     font-size: 16px;
     display: none;
     @media screen and (max-width: 767px) {
          display: block;
     }
}
.header__bar,
.header__barclose {
     color: white;
     font-size: 30px;
     padding: 20px;
     cursor: pointer;
     display: none;
     position: absolute;
     transition: 0.15s ease;
     right: 20px;
     top: 50%;
     transform: translateY(-50%);
     @media screen and (max-width: 767px) {
          display: block;
          padding: 10px;
          z-index: 99;
     }
     &.active1 {
          opacity: 0;
     }
}
.header__barclose {
     margin-right: 3px;
     z-index: -1;
     color: $TextClr2 !important;
     z-index: 9;
}
.header__list.active1 {
     transform: scaleY(1);

     & ~ .header__overlay {
          width: 100%;
     }
}
.header__list {
     position: fixed;
     display: flex;
     text-decoration: none;
     list-style: none;
     gap: 25px;
     top: 0;
     transition: 0.35s ease;
     align-items: flex-start;
     padding: 80px;
     padding-left: 60px;
     padding-right: 100px;
     display: none;
     @media screen and (max-width: 767px) {
          display: block;
          position: absolute;
          top: 0px;
          right: 0px;
          transform: translateX(100%);
          flex-direction: column;
          background-color: white;
          height: 100vh;
          opacity: 0.98;
          box-shadow: 0 0 2px $TextClr2;
          gap: 0px;
     }

     &--item {
          cursor: pointer;
          font-size: 14px;
          color: $TextClr2;
          font-weight: 500;
          line-height: 60px;
          position: relative;
          display: none;
          @media screen and (max-width: 767px) {
               display: block;
               & > i {
                    min-width: 30px;
               }
          }

          &:after {
               content: "";
               position: absolute;
               bottom: 10px;
               left: 0;
               width: 0%;
               background-color: $TextClr2;
               height: 2px;
               border-radius: 100rem;
               transition: 0.45s ease;
          }
          &.active1 {
               color: $mainClr !important;
          }

          &:hover {
               color: $TextClr2;
               &:after {
                    width: 70%;
               }
          }
     }
}
.header__title__h1 {
     font-size: 22px;
     color: $mainClr;
     @media screen and (max-width: 767px) {
          display: none;
     }
}
