@import "../../scss/variable";
.course__container {
     &.full {
          padding-top: 0px;
          padding-bottom: 0px;
          & .course__number {
               display: none;
          }
          & .course__youtube {
               position: fixed;
               top: 0;
               left: 0;
               right: 0;
               z-index: -1;
          }
          & .course__nav {
               background-color: transparent;
               border: none;
               & li {
                    background-color: transparent;
                    color: white;
                    &:nth-child(4) {
                         display: flex;
                         padding-top: 20px;
                         padding-bottom: 17px;
                         &:active {
                              opacity: 0.5;
                              transform: translateY(-5px);
                         }
                    }
                    &:nth-child(5) {
                         display: flex;
                         &:active {
                              opacity: 0.5;
                              transform: translateY(5px);
                         }
                    }
               }
          }

          & .course__record,
          .course__record-key {
               background-color: rgba(0, 0, 0, 0.184);
               & p {
                    color: white;
                    text-shadow: 0 0 2px rgb(43, 43, 43);
               }
               & textarea {
                    background-color: rgba(0, 0, 0, 0);
                    font-weight: 700;
                    color: white;
                    text-shadow: 0 0 3px rgb(43, 43, 43);
                    &::placeholder {
                         color: white;
                    }
               }
               & .record__trans {
                    color: white;
                    background-color: black;
               }
               & .record__save {
                    color: white;
                    background-color: black;
               }
               & .record__close {
                    color: white;
                    background-color: black;
               }
          }
          & .course__record-key {
               background-color: rgba(0, 0, 0, 1) !important;
               & p {
                    color: white;
                    text-shadow: 0 0 0px rgb(43, 43, 43);
               }
               & textarea {
                    background-color: rgba(0, 0, 0, 0);
                    font-weight: 500;
                    color: white;
                    text-shadow: 0 0 0px rgb(43, 43, 43);
                    &::placeholder {
                         color: white;
                    }
               }
          }
     }
     padding-top: 50px;
     padding-bottom: 21px;
     position: relative;
     width: 100%;
     height: 100vh;
     background: white;
     z-index: 998;
     display: flex;
     justify-content: center;
     align-items: center;
     gap: 0px;
     @media screen and (max-width: 767px) {
          flex-direction: column-reverse;
          padding-top: 115px;
          overflow: hidden;
     }
}
.course__youtube {
     position: relative;
     flex: 50;
     height: 100%;
     @media screen and (max-width: 767px) {
          width: 100%;
          flex: 1;
          margin-left: 0px;

          & iframe {
               outline: none;
               border: none;
               @include abs(full);
          }
     }

     & iframe {
          outline: none;
          border: none;
          @include abs(full);
     }
}

.course__number {
     width: 100%;
     height: 100%;
     flex: 1;
     min-width: 350px;
     overflow: scroll;
     padding-top: 75px;
     border-right: 2px solid $activeClr;
     margin-right: 1px;
     & > li {
          &:first-child {
               border-bottom: 0.2px solid $activeClr;
               padding: 20px;
               font-weight: 500;
               color: black;
               font-size: 15px;
          }
     }
     @media screen and (max-width: 767px) {
          max-width: 100%;
          height: 100%;
          padding-top: 0px !important;
          flex: 1;
     }
}
.course__scrol {
     overflow-y: scroll;
}
.course__number {
     -ms-overflow-style: none;
     scrollbar-width: none;
}
.course__number::-webkit-scrollbar {
     display: none;
}

.course__number--item {
     &-title {
          line-height: 1.8;
     }
     & .course__duration {
          font-size: 13px;
          opacity: 0.8;
     }
     &.disable:not(:nth-child(1)) {
          pointer-events: none;
          opacity: 0.8;
          background-color: $activeClr;
          & .course__lock {
               display: block;
          }
          & .course__unlock {
               display: none;
          }
     }

     &.active {
          & .course__icon {
               display: block;
          }
          & .course__lock {
               display: none;
          }
          & .course__unlock {
               display: none;
          }
     }

     &.active1 {
          background-color: $mainClrA;
     }
     width: 100%;
     display: flex;
     padding-left: 14px;
     font-weight: 400;
     overflow-x: hidden;
     justify-content: space-between;
     padding-right: 20px;
     color: $TextClr2;
     font-size: 13.5px;
     align-items: center;
     border-bottom: 0.2px solid $activeClr;
     min-height: 50px;
     cursor: pointer;
     transition: 0.45s ease;
     position: relative;
     &::after {
          content: "";
          @include abs(full);
     }
     &:hover {
          background-color: $hoverClr;
     }
}
.course__icon {
     color: $mainClr;
     display: none;
     font-weight: 700;
}
.course__lock {
     color: $TextClr2;
     opacity: 0.6;
     display: none;
}
.course__unlock {
     color: $TextClr2;
     opacity: 0.65;
     color: $mainClr;
}
.course__title {
     height: 70px;
     width: 348px;
     display: flex;
     justify-content: center;
     align-items: center;
     flex-direction: column;
     position: absolute;
     background-color: white;
     top: 55px;
     z-index: 1;
     @media screen and (max-width: 767px) {
          width: 100%;
          height: 60px;
     }

     & .course__back {
          color: $TextClr2;
          transition: 0.35s ease;
          position: absolute;
          left: 15px;

          cursor: pointer;
          padding: 30px 0px;
          padding-left: 10px;
          font-size: 24px;
          opacity: 0.8;

          &:hover {
               transform: translateX(-2px);
          }
     }
     & i {
          transition: 0.35s ease;
          position: absolute;
          width: 0%;
          height: 100%;
          top: 0;
          left: 0;
          z-index: -1;
          background-color: rgba(0, 255, 242, 0.247);
     }

     & .course__name {
          align-self: flex-end;
          margin-bottom: 9px;

          color: $TextClr2;
     }
     & .course__percents {
          font-size: 12.5px;
          align-self: flex-end;

          color: $TextClr2;
          font-weight: 400;
          & > div {
               position: absolute;
               font-size: 11.8px;
               top: 2px;
               right: 5px;
          }
     }
}
.course__scoll {
     overflow: auto !important;
}

