.course__nav {
     position: fixed;
     top: 0px;
     width: 80px;
     right: 0;
     background-color: rgb(255, 255, 255);
     border-left: 1.5px solid $activeClr;
     user-select: none;
     @media screen and (max-width: 767px) {
          background-color: rgba(255, 255, 255, 0);
          z-index: 999999;
          border-left: none;
          width: 40px;
          top: 270px;
          right: 0;
     }

     &--list {
          display: flex;
          flex-direction: column;
          justify-content: center;
          align-items: center;
          padding-top: 120px;
          list-style-type: none;
          gap: 10px;
          &-item {
               @include flex(center);
               flex-direction: column;
               gap: 10px;
               width: 98%;
               padding: 12px 5px;
               border-radius: 5px;
               color: $TextClr2;
               cursor: pointer;
               background: white;
               transition: 0.35s ease;
               @media screen and (max-width: 767px) {
                    padding: 10px 5px;
                    box-shadow: 0 0 3px $TextClr2;
               }
               &:nth-child(3) {
                    margin-bottom: 30px;
               }
               &:nth-child(4) {
                    margin-bottom: -30px;
                    & i {
                         font-size: 29px;
                    }
                    display: none;
               }
               &:nth-child(5) {
                    display: none;
                    & i {
                         font-size: 29px;
                    }
               }
               &:hover {
                    background-color: $activeClr;
                    @media screen and (max-width: 767px) {
                         background-color: white;
                    }
               }
               & i {
                    font-size: 22px;
                    @media screen and (max-width: 767px) {
                         font-size: 15px;
                    }
               }
               & span {
                    font-size: 13px;
                    white-space: nowrap;
                    @media screen and (max-width: 767px) {
                         display: none;
                    }
               }
               &.active {
                    background-color: $activeClr;
               }
          }
     }
}
.course__record,
.course__record-key {
     position: absolute;
     display: flex;
     justify-content: flex-start;
     align-items: center;
     padding-top: 45px;
     flex-direction: column;
     height: 600px;
     top: 60px;
     width: 600px;
     right: 5px;
     border-radius: 10px;
     overflow: scroll;
     background-color: rgb(255, 255, 255);
     box-shadow: 0px 0px 5px $TextClr2;
     z-index: -1;
     transition: 0.3s ease;
     transform: translateX(120%);
     &.trans {
          background-color: rgba(0, 0, 0, 0.144);
          & p {
               color: white;
               text-shadow: 0 0 2px rgb(43, 43, 43);
          }
          & textarea {
               background-color: rgba(0, 0, 0, 0);
               font-weight: 700;
               color: white;
               text-shadow: 0 0 3px rgb(43, 43, 43);
               &::placeholder {
                    color: white;
               }
          }
          & .record__trans {
               color: white;
               background-color: black;
          }
          & .record__save {
               color: white;
               background-color: black;
          }
          & .record__close {
               color: white;
               background-color: black;
          }
     }
     @media screen and (max-width: 767px) {
          height: 500px;
          width: 85%;
          top: 80px;
          right: 30px;

          z-index: 99999999;
     }
     & .record__close {
          position: absolute;
          top: 5px;
          right: 10px;
          padding: 10px 15px;
          font-size: 20px;
          transition: 0.35s ease;
          border-radius: 10px;
          cursor: pointer;
          color: $TextClr2;
          &:hover {
               background-color: $activeClr;
          }
     }

     & .record__save,
     .record__trans {
          position: absolute;
          bottom: 8px !important;
          right: 10px;
          padding: 10px 15px;
          font-size: 20px;
          transition: 0.35s ease;
          border-radius: 10px;
          cursor: pointer;
          overflow: hidden;
          opacity: 0.7;
          z-index: 1;
          &:after {
               content: "";
               @include abs(full);
               transition: 0.45s ease;
               top: 0;
               left: 0px;
               background: $mainClr;
               opacity: 1;
               width: 0%;
               z-index: -1;
          }
          &.saved {
               opacity: 1;
               color: white;
               &:after {
                    width: 100%;
               }
               & > span {
                    font-size: 16px;
                    padding: 0px 5px;
                    transition: 0.15s ease;
               }
          }
          & > span {
               font-size: 16px;
               padding: 0px 5px;
               user-select: none;
          }
     }
     & .record__trans {
          left: 10px;
          z-index: 0;
          width: max-content;
     }
     &-title {
          color: $TextClr2;
          @media screen and (max-width: 767px) {
               font-size: 12px;
          }
     }
     &-input {
          margin-top: 25px;
          width: 540px;
          line-height: 1.7;
          font-size: 14px;
          height: 100%;
          outline: none;
          border: none;
          text-align: justify;
          overflow: auto;
          @media screen and (max-width: 767px) {
               width: 90%;
               font-size: 12px;
          }
     }
     &.active:nth-child(1) {
          transform: translateX(0%);
          z-index: 99;
          & ~ .course__nav {
               & > .course__nav--list {
                    & > .course__nav--list-item:nth-child(1) {
                         background-color: $activeClr;
                         @media screen and (max-width: 767px) {
                              background-color: $background;
                         }
                    }
               }
          }
     }
     &.active:nth-child(2) {
          transform: translateX(0%);
          z-index: 99;
          & ~ .course__nav {
               & > .course__nav--list {
                    & > .course__nav--list-item:nth-child(2) {
                         background-color: $activeClr;
                         @media screen and (max-width: 767px) {
                              background-color: $background;
                         }
                    }
               }
          }
     }
}
.course__record-key {
     width: 900px;
     height: 620px;
     @media screen and (max-width: 767px) {
          width: 320px;
          & textarea {
               width: 290px;
          }
     }
     & textarea {
          margin-top: 25px;
          width: 840px;
          line-height: 1.7;
          font-size: 14px;
          height: 100%;
          outline: none;
          border: none;
          text-align: justify;
          overflow: auto;
          @media screen and (max-width: 767px) {
               width: 90%;
               font-size: 12px;
          }
     }
}
.ques__container {
     position: absolute;
     width: 100%;
     height: 100%;
     background-color: rgba(0, 0, 0, 0.025);
     top: 0;
     left: 0;
     opacity: 0;
     z-index: -1;
     transition: 0.35s ease;
     &.active {
          opacity: 1;
          z-index: 9999;
     }
}
.record__ques {
     @include abs(center);
     background-color: rgb(255, 255, 255);
     width: 250px;
     height: 130px;
     top: 40%;
     display: flex;
     flex-direction: column;
     justify-content: space-around;
     align-items: center;
     border-radius: 10px;
     box-shadow: 0 0 2px $TextClr2;
     transition: 0.35s ease;

     & > p {
          opacity: 0.9;
     }
     &--buttons {
          display: flex;
          justify-content: space-around;
          align-items: center;
          width: 100%;
     }
     &--item {
          padding: 4px 10px;
          color: white;
          box-shadow: 0 0 2px $TextClr2;
          border-radius: 5px;
          cursor: pointer;
          transition: 0.35s ease;
          font-size: 14px;
          background-color: $TextClr;

          &:hover {
               opacity: 0.9;
          }
     }
}
